.ColorForm {
    width: 100vw;
    height: 100vh;
    background: linear-gradient(
        90deg,
        rgba(255, 0, 0, 1) 0%,
        rgba(255, 154, 0, 1) 10%,
        rgba(208, 222, 33, 1) 20%,
        rgba(79, 220, 74, 1) 30%,
        rgba(63, 218, 216, 1) 40%,
        rgba(47, 201, 226, 1) 50%,
        rgba(28, 127, 238, 1) 60%,
        rgba(95, 21, 242, 1) 70%,
        rgba(186, 12, 248, 1) 80%,
        rgba(251, 7, 217, 1) 90%,
        rgba(255, 0, 0, 1) 100%
    );
}

.ColorForm-box {
    display: absolute;
    position: relative;
    width: 400px;
    height: 200px;
    left: 40%;
    top: 40%;
    border-radius: 15px;
    background: rgba(0,0,0, .3)
}

.ColorForm-box {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: white;
    font-size: 2rem;
}

.ColorForm-box button {
    background-color:rgba(95, 21, 242, 1);
    border-radius: 15px;
    width: 60px;
    height: 25px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: white;
}